import './App.css';
import React,{useState, useRef, useEffect} from 'react';

/* -display function that iterates the array
  -calculating function(loop) that followes rules:
    punkt vor strich rechnung (Wenn * oder / vorhanden, dann die zahlen verrechnen)
    replace the 3 bits with the result
    go again



*/
function App() {
  
  const [userInput, setUserInput] = useState([]);
  const [result, setResult] = useState(null);

  const buttonArray = [{
    id:"equals",
    sign: "=",
  },{
    id:"zero",
    sign: 0,
  },{
    id:"one",
    sign: 1,
  },{
    id:"two",
    sign: 2,
  },{
    id:"three",
    sign: 3,
  },{
    id:"four",
    sign: 4,
  },{
    id:"five",
    sign: 5,
  },{
    id:"six",
    sign: 6,
  },{
    id:"seven",
    sign: 7,
  },{
    id:"eight",
    sign: 8,
  },{
    id:"nine",
    sign: 9,
  },{
    id:"add",
    sign:"+",
  },{
    id:"subtract",
    sign:"-",
  },{
    id:"multiply",
    sign:"*",
  },{
    id:"divide",
    sign:"/",
  },{
    id:"decimal",
    sign:".",
  },{
    id:"clear",
    sign:"C",
  },];

  const handleKeyDown = (sign) => {

    if(sign === "C"){
      setUserInput([]);
      return;
    } else if(sign === "="){
      evaluate();
      return
    }
    
    setUserInput([...userInput, sign]);
  };
  /* evaluation Rules:
     multiply divise first
     if a minus is present, its a negative number
  
  */
  const evaluate = () => {
    try {
      // Join the input array into a string
      const expression = userInput.join("");
      
      // Use eval to calculate the result (eval should be used carefully)
      const calculatedResult = eval(expression);

      // Set the result and reset the input
      setResult(calculatedResult);
      setUserInput([calculatedResult]);
    } catch (error) {
      setResult("Error");
    }
  };

  return (
    <div className="App">
      <div id="calculcator">
        {buttonArray.map((obj)=> <button id={obj.id} onClick={()=>handleKeyDown(obj.sign)}>{obj.sign}</button>)}
      </div>
      <div id="display">{userInput.join("")}</div>
    </div>
  );
}

export default App;
